import React, { Component } from "react";
import { connect } from "react-redux";
import FormCiok from "./Component/FormCiok";
import FormRongsok from "./Component/FormRongsok";
import {
  setTampungCiok,
  settampungRongsok,
} from "../actions/pembayaranSupplierAction";
import Swal from "sweetalert2";
import { Field, formValueSelector, reduxForm } from "redux-form";
import AccordCash from "./Element/AccordCash";
import AccordTrannsfer from "./Element/AccordTransfer";
import AccordCiok from "./Element/AccordCiok";
import AccordRongsok from "./Element/AccordRongsok";
import Axios from "axios";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const server = process.env.REACT_APP_BACKEND_URL;
class PembayaranSupplierSimpan extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      tanggal: formatedDate,
      bonBayar: "",
      detail: "",
      tanggalInvoice: "",
      noPembayaran: "",
      supplier: "",
    };
  }

  componentDidMount() {
    this.props.dispatch(setTampungCiok());
    this.props.dispatch(settampungRongsok());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.change("tanggal", this.state.tanggal);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmit(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var print = {
      jenis: "CIOK",
      gross: data.bruto,
      harga: "-",
      kadar: data.kadarTransaksi,
      netto: parseFloat(data.netto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    var ciokTemp = JSON.parse(localStorage.getItem("ciokTemp")) || [];
    ciokTemp.push(isi);
    localStorage.setItem("ciokTemp", JSON.stringify(ciokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(setTampungCiok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmit2(data) {
    var kode = data.kodeBarang;
    var split = kode.split("|");
    var isi = {
      kodeBarang: split[0],
      netto: data.netto,
      bruto: data.bruto,
      kadarTransaksi: data.kadarTransaksi,
    };
    var print = {
      jenis: "RONGSOK",
      gross: data.bruto,
      harga: "-",
      kadar: data.kadarTransaksi,
      netto: parseFloat(data.netto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    var rongsokTemp = JSON.parse(localStorage.getItem("rongsokTemp")) || [];
    rongsokTemp.push(isi);
    localStorage.setItem("rongsokTemp", JSON.stringify(rongsokTemp));
    var alatBayarBarang =
      JSON.parse(localStorage.getItem("alatBayarBarang")) || [];
    alatBayarBarang.push(isi);
    localStorage.setItem("alatBayarBarang", JSON.stringify(alatBayarBarang));
    this.props.dispatch(settampungRongsok());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  setCiok() {
    this.props.change("ciokBrutto", this.props.totalCiok);
    this.props.change("ciokNetto", this.props.totalCiok);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  setTotal = (input) => (e) => {
    localStorage.setItem([input], e.target.value);
    this.props.change("totalBayar", this.props.totalBayar);
  };
  setRongsok() {
    this.props.change("rongsokBruto", this.props.totalRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitCash(data) {
    var tampung = [];
    var isi = {
      tunaiRp: data.cash,
      tunaiKurs: data.harga,
      tunaiTipe: "BON GR",
      tunaiNetto: data.cashNetto,
    };
    var print = {
      jenis: "TUNAI",
      gross: data.cash,
      harga: data.harga,
      kadar: "",
      netto: parseFloat(data.cashNetto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    tampung.push(isi);
    localStorage.setItem("totalTunaiRp", data.cashNetto);
    localStorage.setItem("alatBayarTunai", JSON.stringify(tampung));
    this.props.change("totalBayar", this.props.totalBayar);
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Disimpan",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  handleSubmitTransfer(data) {
    var tampung = [];
    var isi = {
      transferKurs: data.harga,
      transferNetto: data.transferNetto,
      transferRp: data.transfer,
      transferTipe: "BON GR",
      accFrom: data.supplier,
      accTo: data.tujuan,
      transferTanggalJanji: data.tanggal,
    };
    var print = {
      jenis: "TRANSFER",
      gross: data.transfer,
      harga: data.harga,
      kadar: "",
      netto: parseFloat(data.transferNetto).toFixed(3),
      pkg: "",
      gwpkg: "",
    };
    var sebelum =
      JSON.parse(localStorage.getItem("PrintPembayaranSupplier")) || [];
    sebelum.push(print);
    localStorage.setItem("PrintPembayaranSupplier", JSON.stringify(sebelum));
    tampung.push(isi);
    localStorage.setItem("totalTrfRp", data.transferNetto);
    localStorage.setItem("alatBayarTrf", JSON.stringify(tampung));
    this.props.change("totalBayar", this.props.totalBayar);
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Disimpan",
      showConfirmButton: false,
      timer: 1500,
    });
  }
  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <div className="col-lg-12" style={{ color: "black" }}>
        <div className="col-lg-12">
          <div className="row">
            <AccordCash onSubmit={(data) => this.handleSubmitCash(data)} />
            <AccordTrannsfer
              onSubmit={(data) => this.handleSubmitTransfer(data)}
            />
            {/* CIOK*/}
            <AccordCiok
              totalCiok={this.props.totalCiok}
              totalBrutoCiok={this.props.totalBrutoCiok}
            />
            {/* RONGSOK */}
            <AccordRongsok
              totalRongsok={this.props.totalRongsok}
              totalBrutoRongsok={this.props.totalBrutoRongsok}
            />
            {/* PUTUS TITIPAN RP */}
            {/* <AccordTitipanRp /> */}
            {/* PUTUS TITIPAN GR */}
            {/* <AccordTitipanGR /> */}
          </div>
        </div>
        <form onSubmit={this.props.handleSubmit} className="col-lg-12">
          <div className="row">
            <div className="col-md-3 col-lg-3">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
                defaultValue={this.state.tanggal}
                onChange={this.changeInput("tanggalInvoice")}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <label htmlFor="">Supplier</label>
              <Field
                name="supplier"
                className="form-control"
                component="select"
                defaultValue="DEFAULT | DEFAULT"
                onChange={this.changeInput("supplier")}
              >
                <option value="DEFAULT | DEFAULT" key="DEFAULT">
                  Silahkan pilih
                </option>
                {this.state.listSupplier.map((list) => (
                  <option
                    value={list.kodeSupplier + "|" + list.namaSupplier}
                    key={list.kodeSupplier}
                  >
                    {list.namaSupplier}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="noBon"
                label="Bon Bayar"
                type="txt"
                component={renderField}
                onChange={this.changeInput("noPembayaran")}
              />
            </div>
            <div className="col-md-3 col-lg-3">
              <Field
                name="detail"
                label="Detail"
                type="text"
                component={renderField}
              />
            </div>
            {/* Row 5 */}
            <div className="col-lg-12">
              <div className="card">
                <div className="row p-3">
                  <div className="col-lg-4 col-md-4">
                    <div className="form-group">
                      <Field
                        name="discount"
                        label="Discount"
                        type="number"
                        component={renderField}
                        defaultValue={0}
                        onChange={this.setTotal("discount")}
                        onBlur={this.setTotal("discount")}
                      />
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4">
                    <div className="form-group">
                      <Field
                        name="bonus"
                        label="Bonus"
                        type="number"
                        component={renderField}
                        defaultValue={0}
                        onChange={this.setTotal("bonus")}
                        onBlur={this.setTotal("bonus")}
                      />
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4">
                    <div className="form-group">
                      <Field
                        name="denda"
                        label="Denda"
                        type="number"
                        component={renderField}
                        defaultValue={0}
                        onChange={this.setTotal("denda")}
                        onBlur={this.setTotal("denda")}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {/* Row 5 */}
            <div className="col-lg-12">
              <div className="card">
                <div className="row p-3">
                  <div className="col-lg-4 col-md-4">
                    <div className="form-group">
                      <Field
                        name="totalBayar"
                        label="Total Bayar"
                        type="number"
                        component={renderField}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="col-lg-6">
                  <div className="text-left"></div>
                </div>
                <div className="col-lg-6">
                  <div className="text-right">
                    <button className="btn btn-primary" type="submit">
                      Simpan
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
        <div
          id="ciokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Ciok
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormCiok
                  onSubmit={(data) => this.handleSubmit(data)}
                  from="CK"
                  data={this.props.tampungCiok}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
        <div
          id="rongsokModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Rongsok
                </h4>
                <button
                  id="dismiss_modal1"
                  name="dismiss_modal1"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body">
                <FormRongsok
                  onSubmit={(data) => this.handleSubmit2(data)}
                  from="RSK"
                  data={this.props.tampungRongsok}
                />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

PembayaranSupplierSimpan = reduxForm({
  form: "PembayaranSupplierSimpan",
  enableReinitialize: true,
})(PembayaranSupplierSimpan);
const selector = formValueSelector("PembayaranSupplierSimpan"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer, discount, bonus, denda } = selector(
    state,
    "harga",
    "cash",
    "transfer",
    "discount",
    "bonus",
    "denda"
  );

  var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"))
    ? JSON.parse(localStorage.getItem("alatBayarTrf"))
    : 0;
  var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"))
    ? JSON.parse(localStorage.getItem("alatBayarTunai"))
    : 0;
  var totalCiok = JSON.parse(localStorage.getItem("totalCiok"))
    ? JSON.parse(localStorage.getItem("totalCiok"))
    : 0;
  var totalRosok = JSON.parse(localStorage.getItem("totalRongsok"))
    ? JSON.parse(localStorage.getItem("totalRongsok"))
    : 0;
  var tunai = bayarTunai[0] === undefined ? 0 : bayarTunai[0].tunaiNetto;
  var transferr = bayarTrf[0] === undefined ? 0 : bayarTrf[0].transferNetto;
  var diskon = discount === undefined ? 0 : discount;
  var bonuss = bonus === undefined ? 0 : bonus;
  var dendaa = denda === undefined ? 0 : denda;
  var tambahan = parseFloat(diskon) + parseFloat(bonuss) - parseFloat(dendaa);
  console.log(tambahan);
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    totalBayar: (
      parseFloat(tunai) +
      parseFloat(transferr) +
      parseFloat(totalCiok) +
      parseFloat(totalRosok) +
      parseFloat(tambahan)
    ).toFixed(3),
  };
})(PembayaranSupplierSimpan);
